};

//e:ato
function latLongToVector3(e: any, a: any, t: any, o: any) {
  var r = (e * Math.PI) / 180,
    i = ((a - 180) * Math.PI) / 180,
    n = -(t + o) * Math.cos(r) * Math.cos(i),
    s = (t + o) * Math.sin(r),
    l = (t + o) * Math.cos(r) * Math.sin(i);
  return new THREE.Vector3(n, s, l); //
};

//
function createVirus(data: any, earthSize: any) {
  let colors = [
    new THREE.Color(0xf9b8b8),
    new THREE.Color(0xfe4242),
    new THREE.Color(0xff0000),
  ]; //
  let virSize = 4; //
  let pointsGroup = new THREE.Group(); //
  let list = JSON.parse(JSON.stringify(data));
  list.forEach((e: { value: number; color: any; position: any[]; }) => {
    e.value >= 10000000 && (e.color = colors[2]); //
    e.value >= 500000 && e.value < 10000000 && (e.color = colors[1]);
    e.value < 500000 && (e.color = colors[0]);
    if (e.position) {
      let virusMaterial = new THREE.SpriteMaterial({
        color: e.color,
        map: new THREE.TextureLoader().load(virusImg),
        side: THREE.FrontSide, //
      }); //
      let Sprite = new THREE.Sprite(virusMaterial); //
      Sprite.scale.set(virSize, virSize, 1); //
      let lat = e.position[1]; //
      let lon = e.position[0]; //
      let s = latLongToVector3(lat, lon, earthSize, 1); //
      Sprite.position.set(s.x, s.y, s.z); //
      Sprite.dotData = e; //dotData
      Sprite.name = "";
      pointsGroup.add(Sprite); //
    }
  });
  pointsGroup.name = "";
  scene.add(pointsGroup); //
};

